#display-id{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 190px;
  width: 100%;
}
#display-screen{
  background-color: #a2af77;
  width: 85%;
  height: 100px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.display{
  font-family: digitalCounter;
  font-size: 2em;
  padding: 5px 2px 5px 2px;
}
.display_equation{
  overflow: hidden;
  text-overflow: ellipsis;
  direction: rtl;
  text-align: left;
}
.display_value{
  overflow: hidden;
  direction: ltr;
  text-align: right;
}

@media only screen and (max-width: 480px){
  #display-screen{
    width: 90%;
  } 
}